ul.wp-block-blocks-course-latest-posts {
	list-style: none;
	padding:0;
	margin:0;
	display: grid;
	grid-template-columns: repeat(3, 1fr); // creates a grid with 3 columns of equal width
	gap: 30px; // adds a 30px gap between the columns
	li {
		img {
			margin-bottom: 10px;
		}

		h5 {
			font-size:20px;
			margin: 0 0 5px;
			line-height: 1.4;
			a {
				color:#000;
			}
		}
		time {
			font-size:12px;
			color: #969696;
		}
		p{
			font-size:14px;
			color: #303030;
			line-height:1.6;
			margin:15px 0 0;
		}
	}
}